// GLOBAL ------------------------------------------------------------------------------*/

$mobile-view: 980px;
$media-xs: 320px; // iphone5   
$media-sm: 480px; //       + 
$media-md: 768px; //     /   
$media-lg: 1024px; //    
$media-xl: 1280px; //     ( 1170  )
$content-width: 960px; //  
$content-gap: 20px; //    (   )


// ANIMATIONS -------------------------------------------------------------------------------*/

$translate-time: 0.3s;
$translate-ease: ease;


// FONTS -------------------------------------------------------------------------------*/

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500|Roboto');

$font-main: 'Roboto', sans-serif; //  
$font-second: 'Montserrat', sans-serif; //  
$font-size-base: 16px; //   
$font-weight-base: 400;
$line-height-base: 1.6; //   
$text-margin: 20px; //     


// COLORS -------------------------------------------------------------------------------*/

$color-base: #383e41; //   
$color-base-invert: #fff; //   
$color-link: #0090cc; //   
$color-hover: #006896; //   
$color-active: $color-hover; //    
$color-accent: #f82951; //   ,      ()
$color-disable: #cdd1d3; //  


// HEADING -------------------------------------------------------------------------------*/

$heading-h1-font: $font-main;
$heading-h1-size: 50px;
$heading-h1-height: 70px;
$heading-h1-weight: 300;
$heading-h1-align: left;
$heading-h1-color: $color-base;

$heading-h2-font: $font-main;
$heading-h2-size: 36px;
$heading-h2-height: 46px;
$heading-h2-weight: 300;
$heading-h2-align: left;
$heading-h2-color: $color-base;

$heading-h3-font: $font-main;
$heading-h3-size: 24px;
$heading-h3-height: 36px;
$heading-h3-weight: 400;
$heading-h3-align: left;
$heading-h3-color: $color-base;

// LISTS -------------------------------------------------------------------------------*/

$list-padding-left: 40px;